*, *::before, *::after {
  box-sizing: border-box;
}

.App {
  padding: 30px;
  display: flex;
  flex-direction: row;
}

.App > div {
  margin-right: 50px;
}

.Todolist {
  padding: 20px;
  border: 4px solid hotpink;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.Todolist h3 {
  margin: 0 0 20px 0;
}

.input {
  padding: 3px 5px;
  border: 2px solid gray;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
}

.input:focus {
  border: 2px solid cornflowerblue;
  outline: 0;
}

.error-input {
  border: 2px solid red;
  outline: 0;
}

.error-input:focus {
  border: 2px solid red;
  outline: 0;
}

.btn {
  padding: 2px 10px;
  background-color: cornflowerblue;
  border: 3px solid cornflowerblue;
  cursor: pointer;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
}

.btn:hover {
  cursor: pointer;
  border: 3px solid hotpink;
}

.btn:active {
  background-color: rgb(85, 116, 172);
}

.btn:disabled {
  background-color: grey;
  color: rgba(0, 0, 0, 0.295);
  border: 3px solid grey;
  cursor: auto;
}

.add-task-block {
  height: 50px;
}

.tasklist {
  padding: 0;
  margin: 5px 0 10px 0;
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.task {
  list-style: none;
  display: flex;
  gap: 10px;
  justify-content: space-between;
  font-weight: bold;
}

.task-done span{
  font-weight: normal;
  opacity: 0.8;
  text-decoration: line-through;
}

.filter-buttons {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.active-filter-btn {
  background-color: hotpink;
  border: 3px solid hotpink;
  color: white;
}

.active-filter-btn:active {
  background-color: hotpink;
}

.error-message {
  color: crimson;
}